.row{
    margin-left:20px;
    color: #ffffff;
}
.posters{
    display:flex;
    padding:20px;
    overflow-x:scroll;
    overflow-y:hidden;
}
.posters::-webkit-scrollbar{
    display:none;
    
}


.poster{
    max-height:250px;
    margin-right:10px;
    cursor: pointer;
}

.smallposter{
    max-height:150px;
    margin-right:10px;
    cursor: pointer;
    
}
.poster:hover{
    transform: scale(1.1);
}
.smallposter:hover{
    transform: scale(1.1);
}